.header-color{
background: #03001e;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fdeff9, #ec38bc, #7303c0, #03001e);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #cb202d, #dc1e28, #3366cc); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
background: #03001e; /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fdeff9, #ec38bc, #7303c0, #03001e); /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #40e0d0, #ff8c00, #ff0080); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
text-align: center;
}

.contacts-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
  /* background: #FBD3E9;  /* fallback for old browsers */ */
  background: -webkit-linear-gradient(to right,  #40e0d0, #ff8c00, #ff0080);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right,  #40e0d0, #ff8c00, #ff0080); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.contacts-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
  background: #FBD3E9;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #40e0d0, #ff8c00, #ff0080);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #40e0d0 #ff8c00 #ff0080); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}


.avatar-img{
  height: 150px;
  padding-top: 5em;
  /* position: center; */
  /* display: block; */
  /* margin-left: auto;
  margin-right: auto; */
  width: auto%;
  z-index: 12;
  margin-top: 50px;
  position: absolute;
  margin-left: -600px;

}

.tbody{
  animation: move 3s;
  -webkit-animation: move 3s;
}
@keyframes move {
  from {
    margin-left: 0%;
    width: 300%;
  }

  to {
    margin-left: 100%;
    width: 100%;
  }
}

@-webkit-keyframes move {
  from {
    margin-left: 0%;
    width: 300%;
  }

  to {
    margin-left: 100%;
    width: 100%;
  }
}


#lh1{
  font-family: fantasy;
  font-weight:100;
  color: white;
  -webkit-text-stroke: 2px black;
  margin-top: 70px;
  animation: move 3s;
  -webkit-animation: move 3s;
}

@keyframes move {
  from {
    margin-top: 100%;
    width: 300%;
  }

  to {
    margin-bottom: 0%;
    width: 100%;
  }
}

@-webkit-keyframes move {
  from {
    margin-left: 100%;
    width: 300%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}



.banner-text {
  margin-top: 50px;
  background-color: black;
  opacity: .8;
  width: 100%;
  margin-left: -10px;
